import { Hono } from 'hono';
import { cors } from 'hono/cors';
import { FeedbackWorkflow } from './workflow';
import { processSingleFeedback, validateFeedbackData } from './feedback-processor';
import { 
    processFeedbackBatch, 
    formatBatchResponse, 
    formatBatchResponseWithStatus,
    validateFeedbackBatch, 
    formatValidationErrors,
    determineHttpStatusCode,
    createErrorResponse
} from './batch-processor';

const app = new Hono();

app.use('/api/*', cors());

app.get('/', (c) => {
    return c.text('Feedback Radar API');
});

// Mock Data Seeder
app.post('/api/seed', async (c) => {
    let requestBody;
    let useJsonInput = false;

    // Try to parse JSON input first
    try {
        const contentType = c.req.header('content-type') || '';
        if (contentType.includes('application/json')) {
            requestBody = await c.req.json();
            useJsonInput = true;
        }
    } catch (parseError) {
        // If JSON parsing fails, fall back to hardcoded seeding
        console.log('JSON parsing failed, using hardcoded seed data:', parseError.message);
        useJsonInput = false;
    }

    // Check if we have valid JSON input with mockData array
    if (useJsonInput && requestBody && requestBody.mockData && Array.isArray(requestBody.mockData)) {
        // Use JSON input format - route through batch processing pipeline
        try {
            const mockDataArray = requestBody.mockData;
            
            // Validate the mock data format
            const validation = validateFeedbackBatch(mockDataArray);
            
            if (!validation.success) {
                const errorResponse = formatValidationErrors(validation);
                return c.json({
                    error: 'Invalid mock data format',
                    ...errorResponse
                }, 400);
            }

            // Process mock data through batch processing pipeline
            const batchResult = await processFeedbackBatch(mockDataArray, c.env);
            const response = formatBatchResponseWithStatus(batchResult);
            const statusCode = determineHttpStatusCode(batchResult);

            // Add seed-specific message
            response.seedMessage = 'Mock data seeded through production pipeline';

            return c.json(response, statusCode);

        } catch (error) {
            console.error('Mock data processing error:', error);
            const errorResponse = createErrorResponse(
                'processing_error',
                'Failed to process mock data',
                { message: error.message },
                500
            );
            return c.json(errorResponse, 500);
        }
    } else {
        // Fall back to hardcoded seeding for backward compatibility
        const { results } = await c.env.DB.prepare(`
        INSERT INTO feedback (content, source, sentiment, urgency_score, urgency_reason, themes, status) VALUES
        ('The login page is broken and I cannot access my account!', 'Support Ticket', 'Negative', 9, 'User cannot access account (critical blocker)', '["Login", "Bug"]', 'New'),
        ('I really like the new dashboard design, it is much cleaner.', 'Twitter', 'Positive', 2, 'Positive feedback, no action needed', '["UI/UX", "Dashboard"]', 'New'),
        ('Can you add a dark mode?', 'Feature Request', 'Neutral', 4, 'Feature request, not urgent', '["Feature Request", "UI/UX"]', 'New'),
        ('Billing is confusing. I do not know why I was charged this amount.', 'Email', 'Negative', 7, 'Billing confusion affects customer trust', '["Billing", "UX"]', 'New'),
        ('Everything is working great, thanks!', 'Chat', 'Positive', 1, 'General praise', '["General"]', 'Archived')
        RETURNING *
      `).all();

        // Trigger workflows to vectorise this data (if available)
        let workflowsTriggered = 0;
        for (const feedback of results) {
            try {
                if (c.env.FEEDBACK_WORKFLOW && typeof c.env.FEEDBACK_WORKFLOW.create === 'function') {
                    await c.env.FEEDBACK_WORKFLOW.create({
                        id: `seed-${feedback.id}`,
                        params: {
                            feedbackId: feedback.id,
                            content: feedback.content,
                            source: feedback.source
                        }
                    });
                    workflowsTriggered++;
                }
            } catch (e) {
                console.warn('Workflow creation failed (may not be available in local dev):', e.message);
            }
        }

        return c.json({
            message: workflowsTriggered > 0
                ? 'Seeded successfully (Background processing started)'
                : 'Seeded successfully (Workflows not available in local dev)',
            results
        });
    }
});

app.post('/api/feedback', async (c) => {
    let content, source, file;

    // Handle both JSON and Multipart (for file uploads)
    const contentType = c.req.header('content-type') || '';

    if (contentType.includes('multipart/form-data')) {
        const body = await c.req.parseBody();
        content = body['content'];
        source = body['source'];
        file = body['file']; // File object
    } else {
        const json = await c.req.json();
        content = json.content;
        source = json.source;
    }

    // Validate the feedback data
    const feedbackData = { content, source, file };
    const validation = validateFeedbackData(feedbackData);
    
    if (!validation.isValid) {
        const errorResponse = {
            error: 'Invalid feedback data',
            summary: validation.summary
        };
        
        // Add field-level errors if present
        if (validation.fieldErrors && Object.keys(validation.fieldErrors).length > 0) {
            errorResponse.fieldErrors = validation.fieldErrors;
        }
        
        // Add general errors if present
        if (validation.generalErrors && validation.generalErrors.length > 0) {
            errorResponse.generalErrors = validation.generalErrors;
        }
        
        // Add warnings if present
        if (validation.warnings && validation.warnings.length > 0) {
            errorResponse.warnings = validation.warnings;
        }
        
        // Legacy support
        errorResponse.details = validation.errors;
        
        return c.json(errorResponse, 400);
    }

    try {
        // Process the feedback using shared pipeline
        const result = await processSingleFeedback(feedbackData, c.env);
        
        // Include validation warnings in successful response if present
        if (validation.warnings && validation.warnings.length > 0) {
            result.warnings = validation.warnings;
        }
        
        return c.json(result);
    } catch (error) {
        console.error('Feedback processing failed:', error);
        const errorResponse = createErrorResponse(
            'processing_error',
            'Failed to process feedback',
            { message: error.message },
            500
        );
        return c.json(errorResponse, 500);
    }
});

// Batch feedback ingestion endpoint
app.post('/api/feedback/batch', async (c) => {
    try {
        // Parse JSON payload
        let requestBody;
        try {
            requestBody = await c.req.json();
        } catch (parseError) {
            const errorResponse = createErrorResponse(
                'validation_error',
                'Invalid JSON payload',
                { message: 'Request body must be valid JSON', parseError: parseError.message },
                400
            );
            return c.json(errorResponse, 400);
        }

        // Check for required 'feedback' array in payload
        if (!requestBody || typeof requestBody !== 'object') {
            const errorResponse = createErrorResponse(
                'validation_error',
                'Invalid request format',
                { message: 'Request body must be a JSON object' },
                400
            );
            return c.json(errorResponse, 400);
        }

        if (!requestBody.feedback) {
            const errorResponse = createErrorResponse(
                'validation_error',
                'Missing feedback array',
                { message: 'Request must include a "feedback" array' },
                400
            );
            return c.json(errorResponse, 400);
        }

        const feedbackArray = requestBody.feedback;

        // Basic request size validation
        const requestSizeLimit = 1024 * 1024; // 1MB limit
        const requestSize = JSON.stringify(requestBody).length;
        if (requestSize > requestSizeLimit) {
            const errorResponse = createErrorResponse(
                'validation_error',
                'Request too large',
                { 
                    message: `Request size (${requestSize} bytes) exceeds limit (${requestSizeLimit} bytes)`,
                    requestSize,
                    limit: requestSizeLimit
                },
                413
            );
            return c.json(errorResponse, 413);
        }

        // Validate the feedback array structure
        const validation = validateFeedbackBatch(feedbackArray);
        
        if (!validation.success) {
            const errorResponse = formatValidationErrors(validation);
            return c.json(errorResponse, 400);
        }

        // Process the batch
        const batchResult = await processFeedbackBatch(feedbackArray, c.env);
        const response = formatBatchResponseWithStatus(batchResult);
        const statusCode = determineHttpStatusCode(batchResult);

        return c.json(response, statusCode);

    } catch (error) {
        console.error('Batch processing error:', error);
        const errorResponse = createErrorResponse(
            'system_error',
            'Internal server error',
            { message: 'Failed to process batch request', error: error.message },
            500
        );
        return c.json(errorResponse, 500);
    }
});

app.get('/api/feedback/:id/similar', async (c) => {
    const id = c.req.param('id');

    // 1. Get the vector for the requested feedback
    // Note: Vectorize doesn't support "get by ID" directly easily without storing the metadata or separate query.
    // Ideally we queried the vector from D1 if we stored it, or we re-generate it. 
    // For this prototype, we'll re-generate the embedding for the *content* of the feedback from D1.

    const { results } = await c.env.DB.prepare('SELECT content FROM feedback WHERE id = ?').bind(id).all();
    if (!results.length) return c.json({ error: 'Feedback not found' }, 404);

    const content = results[0].content;

    // 2. Generate embedding
    const embeddingResp = await c.env.AI.run('@cf/baai/bge-base-en-v1.5', { text: [content] });
    const vector = embeddingResp.data[0];

    // 3. Query Vectorize
    if (!c.env.VECTORIZE_INDEX) {
        console.error("VECTORIZE_INDEX binding is missing. Ensure you are running with --experimental-vectorize-bind-to-prod");
        return c.json({ error: "Search index not configured" }, 503);
    }

    let matches;
    try {
        matches = await c.env.VECTORIZE_INDEX.query(vector, { topK: 5, returnMetadata: true });
    } catch (e) {
        console.error("Vectorize query failed:", e);
        return c.json({ error: "Search failed: " + e.message }, 500);
    }

    // Filter out self
    const similarIds = matches.matches
        .filter(m => m.id !== id.toString())
        .map(m => parseInt(m.id));

    if (similarIds.length === 0) return c.json([]);

    // 4. Fetch details from D1
    // "SELECT * FROM feedback WHERE id IN (...)"
    const placeholders = similarIds.map(() => '?').join(',');
    const similarFeedback = await c.env.DB.prepare(
        `SELECT * FROM feedback WHERE id IN (${placeholders})`
    ).bind(...similarIds).all();

    return c.json(similarFeedback.results);
});

app.get('/api/dashboard', async (c) => {
    try {
        // Aggregate stats via SQL - only for active feedback (New and Pending)
        // Sentiment breakdown (exclude 'Pending' as it's not a valid sentiment)
        const sentimentStats = await c.env.DB.prepare(`
        SELECT sentiment, COUNT(*) as count 
        FROM feedback 
        WHERE status IN ('New', 'Pending')
        AND sentiment IN ('Positive', 'Neutral', 'Negative')
        GROUP BY sentiment
      `).all();

        // Fetch themes only from active feedback (not Acted On or Archived)
        const allThemes = await c.env.DB.prepare(`
        SELECT themes 
        FROM feedback 
        WHERE status IN ('New', 'Pending')
      `).all();

        const themeCounts = {};
        allThemes.results.forEach(row => {
            try {
                const themes = JSON.parse(row.themes);
                if (Array.isArray(themes)) {
                    themes.forEach(t => {
                        themeCounts[t] = (themeCounts[t] || 0) + 1;
                    });
                }
            } catch (e) { }
        });

        // Sort themes
        const topThemes = Object.entries(themeCounts)
            .sort((a, b) => b[1] - a[1])
            .slice(0, 5)
            .map(([name, count]) => ({ name, count }));

        return c.json({
            sentiment: sentimentStats.results || [],
            top_themes: topThemes
        });
    } catch (error) {
        console.error('Dashboard error:', error);
        return c.json({
            error: 'Failed to load dashboard data',
            sentiment: [],
            top_themes: []
        }, 500);
    }
}); // End of dashboard endpoint

app.get('/api/inbox', async (c) => {
    const { results } = await c.env.DB.prepare(`
    SELECT * FROM feedback WHERE status = 'New' OR status = 'Pending' ORDER BY urgency_score DESC, created_at DESC
  `).all();

    // Parse themes for the client
    const parsed = results.map(r => ({
        ...r,
        themes: JSON.parse(r.themes)
    }));

    return c.json(parsed);
});

app.post('/api/inbox/:id', async (c) => {
    const id = c.req.param('id');
    const { status } = await c.req.json();

    await c.env.DB.prepare(`
    UPDATE feedback SET status = ? WHERE id = ?
  `).bind(status, id).run();

    return c.json({ success: true });
});

app.get('/api/images/:key', async (c) => {
    const key = c.req.param('key');
    const object = await c.env.FEEDBACK_BUCKET.get(key);

    if (object === null) {
        return c.text('Object Not Found', 404);
    }

    const headers = new Headers();
    object.writeHttpMetadata(headers);
    headers.set('etag', object.httpEtag);

    return new Response(object.body, {
        headers,
    });
});

export default app;
export { FeedbackWorkflow };
